/*Menu*/
@media (max-width:500px) {
  .headerMenu{
    display: none;
  }
.carousel-img-wrapper{
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
}
  .carousel-img-wrapper img{
    max-height: 100%;
    max-width: 100%;
    object-fit: fill;
  }
  .gallery-image:hover {
    opacity: 0.8;
  }

  .top-section {
    flex-direction: column;
    gap: 5rem;
  }
  .about__subheading {
    font-size: 1.5rem;
  }
  .about__heading {
    font-size: 20px;

  }
  .abt-span {
    background-color: darkblue;
    padding: 0.5rem;
    border-radius: 8px;
    font-size: 25px;

  }
  .about-container{
    margin: auto;
    padding: 5%;
    text-align: justify;
  }

}
@media (min-width:500px) {
  .menuIcon{
    display: none;
  }


  /*Gallery image transition*/
  .gallery-image {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: transform 0.3s;
    border-radius: 5%;

  }
  .gallery-image:hover {
    transform: scale(1.1);
    opacity: 0.8;
  }

}

/*Card*/
.card-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px; /* Set the desired height of the image container */
}

.card-image-container img {
  max-width: 100%;
  max-height: 100%;
  object-fit: unset; /* or object-fit: cover; based on your preference */
}

/*Contact Form*/
.submit-button {
  background-color: #1890ff;
  border-color: #1890ff;
  transition: background-color 0.3s ease;
}

.submit-button:hover,
.submit-button:focus {
  background-color: #40a9ff;
}
.submit-button:active,
.submit-button.active {
  background-color: #096dd9;
}

.form-item {
  opacity: 7;
  transform: translateY(20px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.form-item.active {
  opacity: 1;
  transform: translateY(0);
}



/*About*/
.top-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.left {
  flex: 3;
}
.right {
  flex: 2;
}
.about__subheading {
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.abt-span {
  background-color: darkblue;
  padding: 0.5rem;
  border-radius: 8px;
  color: white;
}

.about__heading {
  /*font-size: 3.6rem;*/
  margin-bottom: 3rem;
}
.about__info {
  margin-bottom: 4rem;
}


.abt-img {
  border: 2px solid grey;
  width: 100%;
  height: auto;
  border-radius: 0%;
}

.carousel-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
}

/*Customer Review*/
.customer-reviews-container {
  overflow: auto;
  padding: 1rem;
  white-space: nowrap;
}

.customer-reviews {
  display: inline-block;
  white-space: normal;
}

.review-card {
  display: inline-block;
  vertical-align: top;
  margin-right: 1rem;
  width: 300px;
  transition: transform 0.3s ease;
  transform-origin: top center;
}

.review-card.active {
  transform: scale(1.1);
}

/*Footer*/
.app-footer {
  background-color: #333;
  color: #fff;
  padding: 2rem;
}

.app-footer-content {
  display: flex;
  justify-content: space-between;

}

.app-footer-section {
  margin-bottom: 2rem;
  color: #fff;
}

.app-footer-section h4 {
  color: #fff;
  margin-bottom: 1rem;
}

.app-footer-section p {
  margin-bottom: 0.5rem;
}

.social-media-links {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.social-media-links a {
  color: #fff;
  font-size: 1.5rem;
  margin: 0 0.5rem;
}

.app-footer-bottom {
  background-color: #222;
  padding: 1rem 0;
  text-align: center;
}

.app-footer-bottom p {
  color: #fff;
  margin: 0;
}

.contactForm h3{
  font-style: italic;
}
.view_gallery p{
  font-style: italic;
}
.Backbutton{
  padding: 2%;
  border-radius: 3px;
  background-color: #fdf6f7;
  color: white;
  text-transform: uppercase;
}
.gallery{
  background-color: white;
}